export default{
  data() {
    return {
      userinfo: {},
      menu: [{
        icon: '&#xe646;',
        title: '我的收获地址',
        url: 'address'
      }, {
        icon: '&#xe64c;',
        title: '更改密码',
        url: 'changePassword'
      }, {
        icon: '&#xe740;',
        title: '电话客服',
        url: 'call'
      }, {
        icon: '&#xe763;',
        title: '关于我们',
        url: 'about'
      }]
    }
  },
  onLoad(option) {
    // console.log(JSON.parse(option.userinfo))
    if(option.userinfo) {
      this.userinfo = JSON.parse(option.userinfo)
    }
  },
  methods: {
    goPersonal() {
      uni.navigateTo({
        url: 'personal'
      })
    },
    selAction(url) {
      if(url === 'call') {
        uni.makePhoneCall({
          phoneNumber: '15979779714' //仅为示例
        })
        return
      }
      uni.navigateTo({
        url: url
      })
    },
    loginOut() {
      this.$store.commit('loginout')
      uni.removeStorage({
        key: 'userinfo',
        success: function (res) {
          console.log('success')
        }
      })
      uni.navigateTo({
        url: '../login/login'
      })
    }
  }
}
